import Note from './Note';
document.addEventListener('DOMContentLoaded', main);
var StickyNotes = /** @class */ (function () {
    function StickyNotes(canvas) {
        var _this = this;
        this.canvas = canvas;
        this.isDragging = false;
        this.isEditing = false;
        this.notesZone = canvas.querySelector('#notes-zone');
        this.trashZone = canvas.querySelector('#trash-zone');
        this.addNote = this.addNote.bind(this);
        this.notesZone.addEventListener('mouseup', function (event) {
            if (event.button === 0) {
                if (_this.isDragging)
                    return;
                if (!event.target.closest('.note'))
                    _this.addNote({ x: event.x, y: event.y });
            }
        });
        this.onDragSet = this.onDragSet.bind(this);
    }
    StickyNotes.prototype.addNote = function (pos) {
        var note = new Note('Some text', this.notesZone, this.onDragSet);
        var noteElement = note.noteElement;
        note.setPosition({
            x: pos.x - noteElement.offsetWidth / 2,
            y: pos.y - noteElement.offsetHeight / 2
        });
    };
    StickyNotes.prototype.onDragSet = function (isDragging) {
        this.isDragging = isDragging;
    };
    return StickyNotes;
}());
function main() {
    var canvas = document.querySelector('#canvas');
    new StickyNotes(canvas);
}
