var Note = /** @class */ (function () {
    function Note(text, parentElement, onDragSet) {
        this.text = text;
        this.parentElement = parentElement;
        this.onDragSet = onDragSet;
        this.isDragging = false;
        this.isEditing = false;
        this.currentDroppable = null;
        this.colors = [
            '#bfff25',
            '#0cffc6',
            '#ff9cbf',
            '#ffc92a',
            '#97b7ff',
            '#daa0ff',
            '#69ff72',
            '#c9fff6',
            '#bbbeff',
        ];
        this.noteElement = document.createElement('div');
        this.noteElement.classList.add('note');
        this.noteElement.style.backgroundColor = this.colors[Math.floor(Math.random() * 10)];
        this.noteElement.innerHTML = "\n        <div class=\"note-inner\">\n            <textarea class=\"text\" placeholder=\"Your text here\">" + this.text + "</textarea>\n        </div>        \n        <span class=\"resize\"></span>\n    ";
        this.resizeNoteHandler = this.resizeNoteHandler.bind(this);
        this.moveNote = this.moveNote.bind(this);
        this.parentElement.appendChild(this.noteElement);
        this.resizingElement = this.noteElement.querySelector('.resize');
        this.resizingElement.addEventListener('mousedown', this.resizeNoteHandler);
        this.noteElement.addEventListener('mousedown', this.moveNote);
    }
    Note.prototype.moveNote = function (event) {
        var _this = this;
        if (event.target === this.resizingElement)
            return;
        // Moving note to front
        var notes = this.parentElement.querySelectorAll('.note');
        for (var i = 0; i < notes.length; ++i) {
            notes[i].style.zIndex = '1';
        }
        this.noteElement.style.zIndex = '1000';
        //
        var shiftX = event.clientX - this.noteElement.getBoundingClientRect().left;
        var shiftY = event.clientY - this.noteElement.getBoundingClientRect().top;
        var moveAt = function (pageX, pageY) {
            _this.setPosition({
                x: pageX - shiftX,
                y: pageY - shiftY,
            });
        };
        var enterDroppable = function (elem) {
            elem.style.backgroundColor = '#FDB7B2';
        };
        var leaveDroppable = function (elem) {
            elem.style.backgroundColor = '#fdd1cf';
        };
        var onMouseMove = function (event) {
            moveAt(event.pageX, event.pageY);
            _this.noteElement.hidden = true;
            var elemBelow = document.elementFromPoint(event.clientX, event.clientY);
            _this.noteElement.hidden = false;
            if (!elemBelow)
                return;
            var droppableBelow = elemBelow.closest('#trash-zone');
            if (_this.currentDroppable != droppableBelow) {
                if (_this.currentDroppable) {
                    leaveDroppable(_this.currentDroppable);
                }
                _this.currentDroppable = droppableBelow;
                if (_this.currentDroppable) {
                    enterDroppable(_this.currentDroppable);
                }
            }
        };
        var onMouseUp = function () {
            document.removeEventListener('mousemove', onMouseMove);
            _this.onDragSet(false);
            if (_this.currentDroppable) {
                _this.removeNote();
                leaveDroppable(_this.currentDroppable);
            }
            _this.noteElement.removeEventListener('mouseup', onMouseUp);
        };
        moveAt(event.pageX, event.pageY);
        document.addEventListener('mousemove', onMouseMove);
        this.noteElement.addEventListener('mouseup', onMouseUp);
        this.noteElement.addEventListener('dragstart', function () { return false; });
    };
    Note.prototype.resizeNoteHandler = function (event) {
        var _this = this;
        this.onDragSet(true);
        var noteWidth = this.noteElement.offsetWidth;
        var noteHeight = this.noteElement.offsetHeight;
        var startX = event.pageX;
        var startY = event.pageY;
        var onNoteResize = function (event) {
            _this.setSize({
                x: noteWidth + (event.pageX - startX),
                y: noteHeight + (event.pageY - startY),
            });
        };
        var onMouseUp = function () {
            _this.onDragSet(false);
            document.removeEventListener('mousemove', onNoteResize);
        };
        document.addEventListener('mousemove', onNoteResize);
        document.addEventListener('mouseup', onMouseUp);
        this.noteElement.addEventListener('mousedown', this.moveNote);
    };
    Note.prototype.setPosition = function (position) {
        this.noteElement.style.left = position.x + 'px';
        this.noteElement.style.top = position.y + 'px';
    };
    ;
    Note.prototype.setSize = function (size) {
        this.noteElement.style.width = size.x + 'px';
        this.noteElement.style.height = size.y + 'px';
    };
    Note.prototype.removeNote = function () {
        this.resizingElement.removeEventListener('mousedown', this.resizeNoteHandler);
        this.parentElement.removeChild(this.noteElement);
    };
    return Note;
}());
export default Note;
